/**
 * Side loaded styles for the CopyId functionality.
 * Note: since the required CopyId javascript functionality might not be supported, these styles are only side loaded when required.
 */

/*================ Classes ================*/

.svg-icon.svg-icon--copy {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  background-color: var(--color-text--subdued);
  -webkit-mask-image: url(iconCopy.svg);
  mask-image: url(iconCopy.svg);
  cursor: pointer;
}

.svg-icon.svg-icon--copy:hover,
.svg-icon.svg-icon--copy:focus,
.svg-icon.svg-icon--copy:active {
  background-color: var(--color-text);
}

.copy-message {
  position: fixed;
  bottom: 0;
  left: 50%;
  margin: 0 auto 5px;
  transform: translateX(-50%);
  color: var(--color-bg);
  background-color: var(--color-text);
  padding: 4px;
  border: 2px solid var(--color-border);
  border-radius: 5px;
  opacity: 0;
  transition: opacity 1.0s;
  pointer-events: none;
}

.copy-message--visible {
  opacity: 1;
  transition: opacity 0.2s;
}
